import React, { Component } from "react";
import { connect } from "react-redux";

import axios from "../../axios-api";
import Order from "../../components/Order/Order";
import Spinner from "../../components/UI/Spinner/Spinner";
import axiosMessageHandler from "../../hoc/axiosMessageHandler/axiosMessageHandler";
import * as actions from "../../store/actions/index";

class Orders extends Component {
  componentWillMount() {
    this.props.onFetchOrder(this.props.token, this.props.userId);
  }
  render() {
    console.log(this.props.data);

    const show = this.props.loading ? (
      <Spinner />
    ) : (
      Object.keys(this.props.data).map((order) => {
        // console.log(this.props.data[order]);

        return (
          <div key={order}>
            <p>order {order}</p>
            {this.props.data[order]._id}{" "}
          </div>
        );
      })
    );
    console.log(show);

    // (
    //   Object.keys(this.props.data).map((igKey) => {
    //     return <Order key={igKey} data={this.props.data[igKey]} />;
    //   })
    // )
    return <div>{show}</div>;
  }
}
const mapStateToProps = (state) => {
  return {
    data: state.order.orders,
    loading: state.order.loading,
    error: state.order.error,
    token: state.auth.token,
    userId: state.auth.userId,
  };
};

const mapDipatchToProps = (dispatch) => {
  return {
    onFetchOrder: (token, id) => dispatch(actions.fetchOrders(token, id)),
  };
};

export default connect(
  mapStateToProps,
  mapDipatchToProps
)(axiosMessageHandler(Orders, axios));



